import React from "react";
import MyOrdersList from "./MyOrdersList";

const MyOrders = (props) => {
//   const { sendRequest, data: loadedOrders } = useHttp(getMyOrders);

//   useEffect(() => {
//     sendRequest();
//   }, []);
//   console.log("b", loadedOrders);

  const orders = props.orders;

  const ordersList = orders.map((orders) => (
    <MyOrdersList
      key={orders.id}
      id={orders.id}
      orderTotalPrice={orders.orderTotalPrice}
      orderTotalQty={orders.orderTotalQty}
    />
  ));

  console.log("c", ordersList);

  return <div>{ordersList}</div>;
};

export default MyOrders;
